.header-section {
  padding: 0 16px;
  background: $hero-bg;
  background-repeat: no-repeat;
  background-image: url('../images/header/header-icecream.png'),
    url('../images/header/header-elipse.png');
  background-position: top 100% right 37%, top 40% right 25%;
}
@media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header-section {
    background-position: top 100% right 37%, top 40% right 25%;
    background-image: url('../images/header/header-icecream@2x.jpg'),
      url('../images/header/header-elipse@2x.png');
  }
}

.menu-container {
  display: none;
}

.header {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 37px;
  &__icon {
    margin: 0 auto 0 0;
  }
  &__list {
    display: flex;
    flex-direction: row;
    gap: 35px;
    list-style: none;
    margin: 0 auto 0 auto;
    padding: 0;
  }
  &__item {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    color: $whitecolor;
    text-decoration: none;
    &:hover,
    &:focus {
      color: $accent-color;
      cursor: pointer;
    }
  }
  &__menu-btn {
    background-color: transparent;
    border: none;
    display: none;
  }

  &__buy-btn {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    text-align: center;
    color: $accent-color;
    background: $whitecolor;
    border: none;
    border-radius: 22px;
    margin: 0 0 0 auto;
    padding: 12px 53px 12px 37px;
    &:hover,
    &:focus {
      background: $button-hover-color;
      cursor: pointer;
    }
  }
  &__btn-icon {
    margin-left: 16px;
  }
}

.hero {
  justify-content: space-between;
    padding: 60px 0 0;
    display: flex;

  &__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 38px;
    line-height: 44px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffffff;
    padding: 0;
    margin: 0 0 24px 0;
  }

  &__title--color-red {
    color: $accent-color;
  }

  &__btns {
    list-style: none;
    display: flex;
    flex-direction: row;
    gap: 12px;
    padding: 0;
  }
  &__btn {
    font-size: 14px;
    line-height: 18px;
    color: $whitecolor;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 11px 23px;
    background: $accent-color;
    border-radius: 20px;
    border: none;
    cursor: pointer;
  }
  &__btn--white {
    color: $accent-color;
    background: $whitecolor;
    &:hover,
    &:focus {
      background: $button-hover-color;
      cursor: pointer;
    }
  }
  &__link {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    color: $whitecolor;
    text-decoration: none;
    &--red {
      color: $accent-color;
    }
  }
    &__img {
      display:block;
      width: 304px;
      margin-top: 103px;
  }
}

.info {
  display: flex;
  flex-direction: row;
  margin: 40px 0 0 0;
  

  &__side {
    width: 200px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 0;
  }

  &__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: $whitecolor;
    margin: 68px 0 0 0;
  }

  &__btn {
    width: 26px;
    height: 26px;
    border: none;
    border-radius: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $whitecolor;
    &:hover,
    &:focus {
      background: $button-hover-color;
      cursor: pointer;
    }
  }
}

.sell-point {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 0;
  align-items: flex-end;

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__no {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 46px;
    line-height: 53px;
    text-align: right;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $accent-color;
    margin: -23px 10px 0 0;
  }

  &__type {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: right;
    color: $whitecolor;
    margin: 0;
  }
}

@media (max-width: 768px) and (min-width: 390px) {
  .header-section {
    padding: 0 16px;
    background: $hero-bg;
    background-repeat: no-repeat;
    background-image: url('../images/header/header-icecream-t.png'),
      url('../images/header/elipse-t.png');
    background-position: top 100% right 50%, top 60% right 43%;
    padding: 0 32px;
    overflow-y: hidden;
  }
  @media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header-section {
    background-position:  top 100% right 50%, top 60% right 43%;
    background-image: url("../images/header/header-icecream-t@2x.png"), url("../images/header/elipse-t@2x.png");
  }
}
