@media (max-width: 391px) and (min-width: 320px) {
  .header-section {
    padding: 0 16px;
    background: $hero-bg;
    background-repeat: no-repeat;
    background-image: url('../images/header/header-icecream-t.png'),
      url('../images/header/elipse-t.png');
    background-position: top 100% right 3%, top 60% right 43%;
    padding: 0 32px;
  }
  @media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .header-section {
    background-position:  top 100% right 3%, top 60% right 43%;
    background-image: url("../images/header/header-icecream-t@2x.png"), url("../images/header/elipse-t@2x.png");
  }
}
  .sell-point,
  .hero__img,
  .header__buy-btn {
    display:none;
  }
  .header__icon {
    margin-right: 100px;
  }
}
@media (max-width: 321px) {
  .header-section {
    // overflow-y: hidden;
    background: #ffb8ca;
    background-repeat: no-repeat;
    background-image: url('../images/header/icecream-m.png'),
      url('../images/header/elipse-m.png');
    background-position: top 100% right 30%, top 60% right 0%;
  }
  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .header-section {
      background-position: top 100% right 30%, top 60% right 0%;
      background-image: url('../images/header/icecream-m@2x.png') 171px,
        url('../images/header/elipse-m@2x.png');
    }
  }
  .header {
    padding: 8px 0 0 0;
    &__icon {
      margin-right: 41px;
      padding: 0;
    }
    &__menu-btn {
      display: initial;
    }
    &__buy-btn {
      display: none;
    }
  }
  .hero {
    padding: 151px 0 158px 0;
    &__title {
      font-size: 26px;
      line-height: 30px;
    }
    &__img,
    .sell-point,
    .info {
      display: none;
    }
  }
  .no-display {
    display: none;
  }
  .menu-toggle {
    visibility: inherit;
  }
}
